import { zhiCardFactory as cardFactory } from '@ali/4ever-factory';
import { NoSupportPlaceholder } from '@ali/we-biz-url-card';
import React from 'react';

interface URLCardPluginConfig {
  locale: any;
  isMobile: boolean;
}

const URLCard = (config: { urlCard: URLCardPluginConfig }) => {
  const isMobile = config.urlCard?.isMobile;
  return {
    cardType: 'url',
    isMobile: isMobile,
    renderNode() {
      // 导出长图时统一返回占位符
      return <NoSupportPlaceholder />;
    },
  };
};

export default cardFactory(URLCard);
